//import abi from './BuyMeACoffee.json';
//import { ethers } from "ethers";
//import React, { useEffect, useState } from "react";
//const { ethers } = require('ethers');
import { ethers } from "ethers";
import React, { useEffect, useState} from "react";
import { Link } from 'react-router-dom';
//const { Web3Provider } = require("@ethersproject/providers");
//const provider = new ethers.providers.Web3Provider(window.ethereum);
//import styles from '../public/css/Home.modules.css'
function Coffee(){
    const [name, setName] = useState("");
    const [message, setMessage] = useState("");
    const [memos, setMemos] = useState([]);
    const [accountCount, setAccountCount] = useState(0);
    const onNameChange = (event) => {
        setName(event.target.value);
    }
    const onMessageChange = (event) => {
        setMessage(event.target.value);
    }
    let provider;
    const contractAddress = '0xBCbF6c8B18AAC508F739dbf0a146B1418890E1f8';
    const abi = [
        {
          "inputs": [],
          "stateMutability": "nonpayable",
          "type": "constructor"
        },
        {
          "anonymous": false,
          "inputs": [
            {
              "indexed": true,
              "internalType": "address",
              "name": "from",
              "type": "address"
            },
            {
              "indexed": false,
              "internalType": "uint256",
              "name": "timestamp",
              "type": "uint256"
            },
            {
              "indexed": false,
              "internalType": "string",
              "name": "name",
              "type": "string"
            },
            {
              "indexed": false,
              "internalType": "string",
              "name": "message",
              "type": "string"
            }
          ],
          "name": "NewMemo",
          "type": "event"
        },
        {
          "inputs": [
            {
              "internalType": "string",
              "name": "_name",
              "type": "string"
            },
            {
              "internalType": "string",
              "name": "_message",
              "type": "string"
            }
          ],
          "name": "buyCoffee",
          "outputs": [],
          "stateMutability": "payable",
          "type": "function"
        },
        {
          "inputs": [
            {
              "internalType": "string",
              "name": "_name",
              "type": "string"
            },
            {
              "internalType": "string",
              "name": "_message",
              "type": "string"
            }
          ],
          "name": "buyLargeCoffee",
          "outputs": [],
          "stateMutability": "payable",
          "type": "function"
        },
        {
          "inputs": [],
          "name": "getMemos",
          "outputs": [
            {
              "components": [
                {
                  "internalType": "address",
                  "name": "from",
                  "type": "address"
                },
                {
                  "internalType": "uint256",
                  "name": "timestamp",
                  "type": "uint256"
                },
                {
                  "internalType": "string",
                  "name": "name",
                  "type": "string"
                },
                {
                  "internalType": "string",
                  "name": "message",
                  "type": "string"
                }
              ],
              "internalType": "struct BuyMeACoffee.Memo[]",
              "name": "",
              "type": "tuple[]"
            }
          ],
          "stateMutability": "view",
          "type": "function"
        },
        {
          "inputs": [
            {
              "internalType": "address",
              "name": "_newOwner",
              "type": "address"
            }
          ],
          "name": "setOwner",
          "outputs": [],
          "stateMutability": "nonpayable",
          "type": "function"
        },
        {
          "inputs": [],
          "name": "withdrawTips",
          "outputs": [],
          "stateMutability": "nonpayable",
          "type": "function"
        }
      ];
    let signer = null;
    let buyMeACoffee = new ethers.Contract(contractAddress, abi, signer);
    const connectWallet = async () => {
        if (window.ethereum == null) {
            // If MetaMask is not installed, we use the default provider,
            // which is backed by a variety of third-party services (such
            // as INFURA). They do not have private keys installed so are
            // only have read-only access
            console.log("MetaMask not installed; using read-only defaults")
            provider = ethers.getDefaultProvider();
            console.log("signer: default");
            buyMeACoffee = new ethers.Contract(contractAddress, abi, provider);
            const accounts = await provider.listAccounts();
            setAccountCount(accounts.length);
            return false;
        } 
        else 
        {
            // Connect to the MetaMask EIP-1193 object. This is a standard
            // protocol that allows Ethers access to make all read-only
            // requests through MetaMask.
            provider = new ethers.BrowserProvider(window.ethereum);
            // It also provides an opportunity to request access to write
            // operations, which will be performed by the private key
            // that MetaMask manages for the user.
            signer = await provider.getSigner();
            buyMeACoffee = new ethers.Contract(contractAddress, abi, signer);
            const accounts = await provider.listAccounts();
            setAccountCount(accounts.length);
            return true;
        }
    }
    const buyCoffee = async () => {
        if(window.ethereum!=null)
        {
            provider = new ethers.BrowserProvider(window.ethereum);
            signer = await provider.getSigner();
            buyMeACoffee = new ethers.Contract(contractAddress, abi, signer);
        }
        else
        {
            provider = ethers.getDefaultProvider();
            buyMeACoffee = new ethers.Contract(contractAddress, abi, provider);
        }
        console.log("Buying coffee...");
        const coffeeTxn = await buyMeACoffee.buyCoffee(
            name ? name : "Anon",
            message ? message : "Enjoy your coffee!",
            { value: ethers.parseEther("0.001")}
        );
        await coffeeTxn.wait();
        console.log("Mined ", coffeeTxn.hash);
        console.log("Coffee purchased!");
        setName("");
        setMessage("");
        let memosa = await buyMeACoffee.getMemos();
        setMemos(memosa);
    }
    const buyLargeCoffee = async () => {
        if(window.ethereum!=null)
        {
            provider = new ethers.BrowserProvider(window.ethereum);
            signer = await provider.getSigner();
            buyMeACoffee = new ethers.Contract(contractAddress, abi, signer);
        }
        else
        {
            provider = ethers.getDefaultProvider();
            buyMeACoffee = new ethers.Contract(contractAddress, abi, provider);
        }
        console.log("Buying large coffee...");
        const coffeeTxn = await buyMeACoffee.buyCoffee(
            name ? name : "Anon",
            message ? message : "Enjoy your large coffee!",
            { value : ethers.parseEther("0.003")}
        );
        await coffeeTxn.wait();
        console.log("Mined ", coffeeTxn.hash);
        console.log("Coffee purchased!");
        setName("");
        setMessage("");
        let memosa = await buyMeACoffee.getMemos();
        setMemos(memosa);
    }
    useEffect(() => {
        const fetchMemos = async () => {
            if(window.ethereum!=null)
            {
                provider = new ethers.BrowserProvider(window.ethereum);
                signer = await provider.getSigner();
                buyMeACoffee = new ethers.Contract(contractAddress, abi, signer);
                console.log("Fetching memos from the blockchain...");
                let memosa = await buyMeACoffee.getMemos();
                console.log("fetched!");
                setMemos(memosa);
                console.log(memosa.length);
            }
            else
            {
                provider = ethers.getDefaultProvider();
                buyMeACoffee = new ethers.Contract(contractAddress, abi, provider);
                console.log("Fetching memos from the blockchain...");
                let memosa = await buyMeACoffee.getMemos();
                console.log("fetched!");
                setMemos(memosa);
            }
        };
        fetchMemos();
      }, []);
    return (
        <div>
          <main>
            <h1>Buy Daniel a Coffee!</h1>
            {accountCount ? (
              <div>
                <form><div><label>
                    Name</label><br />
                    <input
                        id="name"
                        type="text"
                        placeholder="anon"
                        onChange={(onNameChange)}/>
                    </div>
                    <br />
                    <div>
                        <label>
                            Send Daniel a message
                        </label>
                        <br />

                        <textarea
                        rows={3}
                        placeholder="Enjoy your coffee!"
                        id="message"
                        onChange={onMessageChange}
                        required>
                        </textarea>
                    </div>
                    <div>
                        <button
                        type="button"
                        onClick={buyCoffee}
                        >
                            Send 1 coffee 0.001 ETH
                        </button>
                    </div>
                    <div>
                        <button
                        type="button"
                        onClick={buyLargeCoffee}
                        >
                            Send 1 large coffee for 0.003 ETH
                        </button>
                    </div>
                </form>
              </div>
            ) : (
              <div>
                <hr />
              <button onClick={connectWallet} style={{ width: "200px", height: "50px" }}>Connect Wallet</button>   
              </div>

            )}
          </main>
          <hr />
          <p>Memos </p>
          {memos?.slice(-Math.min(memos.length, 6)).map((memo, idx) => (
            <div
              key={idx}
              style={{ border: "2px solid", borderRadius: "5px", padding: "5px", margin: "5px" }}
            >
              <p style={{ fontWeight: "bold" }}>"{memo.message}"</p>
              <p>
                From: {memo.name} at {""} {new Date(Number(memo.timestamp) * 1000).toLocaleString()}
              </p>
            </div>
          ))}
          <br />
          <hr />
          <Link to="/" style={{ marginRight: '10px' }}>Home</Link>
          <Link to="/Contact">Contact</Link>
        </div>
      );
}
export default Coffee; 